import React, { useState } from 'react';
import Button from '../../../components/Button';
import { LightSubTitle, LightText } from '../../../styles/common';
import * as Styled from './styles';
import model from '../../../assets/images/3d2.png';
import emailjs from '@emailjs/browser';

export default function ContactSection() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');

    function sendMail() {
        const templateParams = {
            from_name: name,
            message: message,
            from_email: email
        }

        emailjs.send('service_r3w5ze8', 'template_q1l5gpe', templateParams, 'MOS0Lfg3Oqy9LQ5sK')
            .then(function(response) {
                console.log('SUCCESS!', response.status, response.text);
                setName('');
                setEmail('');
                setMessage('');
            }, function(error) {
                console.log('FAILED...', error);
            });
    }

    return (
        <Styled.ContactSection id="contact"> 
            <Styled.Form onSubmit={(e) => { e.preventDefault() }}>
                <LightSubTitle>Talk to us</LightSubTitle>

                <LightText>drop us a letter <Styled.Link>sales@jito.dev</Styled.Link> or contact via form</LightText>

                <Styled.Label>Name*</Styled.Label>
                <Styled.Input placeholder='Enter name' type='text' value={name} onChange={(e) => setName(e.target.value)} />

                <Styled.Label>Email*</Styled.Label>
                <Styled.Input placeholder='Enter email' type='email' value={email} onChange={(e) => setEmail(e.target.value)} />

                <Styled.Label>Message</Styled.Label>
                <Styled.Textarea rows={4} placeholder='Enter message' value={message} onChange={(e) => setMessage(e.target.value)} />

                <Button type="button" onClick={sendMail} colorReverse withBorder >Submit</Button>
            </Styled.Form>
        
            <Styled.Model src={model} />
        </Styled.ContactSection>
  )
}
